.modal__body-top-promo,
.modal__body-promo {
  background-color: #323232;
  margin: -28px -28px 32px;
  padding: 48px 32px 24px;
  border-radius: 5px 5px 0 0;

  .modal__title {
    text-align: center;
    color: #fff;
    margin: 0 0 24px;

    &-small {
      display: block;
      font-size: 15px;
      line-height: 160%;
      font-weight: 400;
      margin-bottom: 8px;
    }

    &-main {
      display: block;
      font-size: 48px;
      line-height: 120%;
      font-weight: 500;

      span {
        color: #5EA9FF;
      }
    }
  }
}

.modal__body-promo {
  margin-bottom: -28px;
  border-radius: 5px;
  padding-top: 48px;
  padding-bottom: 48px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 412px;

  .modal__title {
    font-size: 48px;
    line-height: 120%;
    font-weight: 500;
    margin: 0;

    & + .modal__desc {
      margin-top: 8px;
    }
  }

  .modal__desc {
    text-align: center;
    margin: 0;

    & + div {
      width: 100%;
    }

    & + .button {
      margin-top: 32px;
    }
  }

  .modal__form-simple {
    margin-top: 32px;
    width: 100%;
  }

  .modal__form-bottom-close {
    margin-bottom: 24px;

    .modal__close {
      color: #fff;
    }
  }
}

.modal-card__card-link {
  display: flex;
  border-radius: 8px;
  border: none;
  background-color: #6C7684;
  padding: 0;
  overflow: hidden;

  &:hover {
    text-decoration: none;
  }

  .modal-card__card-img-block {
    width: 100px;
    height: 100px;
    background-color: #fff;
    position: relative;
    margin: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
  }

  img.modal-card__card-img-icon,
  img.modal-card__card-img-icon-mobile {
    position: absolute;
    top: 0;
    left: 0;
    width: 32px;
    height: 32px;
  }

  img.modal-card__card-img-icon-mobile {
    display: none;
  }

  .modal-card__card-content {
    display: flex;
    align-items: center;
    position: relative;
    flex: 1;
    padding: 24px 72px 24px 24px;
  }

  .modal-card__card-name {
    color: #fff;
    margin: 0;
    text-align: left;

    span {
      &:first-child {
        display: block;
        font-size: 13px;
        line-height: 160%;
      }

      &:last-child {
        display: block;
        font-size: 15px;
        line-height: 120%;
        font-weight: 500;
      }
    }
  }

  .modal-card__card-link-arrow {
    position: absolute;
    top: 50%;
    right: 24px;
    transform: translateY(-50%) rotate(180deg);
    color: #fff;
    width: 24px;
    height: 24px;
    filter: invert(100%);
  }
}

.modal-card__counter {
  text-align: center;
  color: #6C7684;
  padding-top: 24px;

  &-title {
    display: block;
    margin-bottom: 8px;
  }

  &-content {
    display: flex;
    justify-content: center;
    gap: 16px;
  }

  &-item {
    min-width: 64px;

    span {
      display: block;
    }
  }

  &-value {
    font-size: 19px;
    line-height: 120%;
    font-weight: 500;
    color: #fff;
  }
}

.modal-card__feedback {
  color: #6C7684;
  text-align: center;
  margin-bottom: 32px;
}

.modal__form-simple {
  form {
    display: flex;
  }

  .modal__form-input-block {
    flex: 1;
  }

  .modal__form-input {
    width: 100%;

    label {
      width: 100%;
    }

    input {
      background-color: #F4F4F8;
      height: 48px;
      border: none;
      transition: background-color 0.2s;
      font-size: 15px;

      &:hover {
        background-color: #E6E6EF;
      }
    }

    input::-webkit-input-placeholder,
    input::-moz-placeholder,
    input:-ms-input-placeholder,
    input::placeholder {
      font-size: 15px;
    }
  }

  .modal__form-bottom {
    margin-left: 16px;
  }
}

.modal__form-bottom-close {
  text-align: center;
  margin-bottom: 32px;

  .modal__close {
    font-size: 15px;
    line-height: 160%;
    position: static;
    width: auto;
    height: auto;
    text-decoration: underline;
  }
}

.modal__form-policy {
  text-align: center;
  color: #6C7684;
  margin-bottom: 0;
  font-size: 13px;
  max-width: 435px;
}

.modal__close--dark {
  background-image: url('data:image/svg+xml;utf8,<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M5 5L19 19" stroke="rgba(108, 118, 132, 1)" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/><path d="M19 5L5 19" stroke="rgba(108, 118, 132, 1)" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/></svg>');
}

@media (max-width: 767px) {
  .modal__body-top-promo {
    margin: -30px -15px 24px;
    padding: 86px 15px 24px;
    border-radius: 0;

    .modal__title-main {
      font-size: 32px;

      span {
        white-space: nowrap;
      }
    }
  }

  .modal__body-promo {
    min-height: calc(100% + 60px);
    margin: -30px -15px;
    padding: 86px 15px 100px;

    .modal__title {
      font-size: 32px;
    }

    .button {
      width: 100%;
    }

    .modal__title-main {
      font-size: 32px;
    }
  }

  .modal-card__card-link {
    padding: 15px;

    .modal-card__card-img-block {
      width: 64px;
      height: 64px;
      border-radius: 7px;
    }

    .modal-card__card-content {
      padding: 0 0 0 15px;
      align-items: flex-start;
    }

    .modal-card__card-link-arrow {
      display: none;
    }

    img.modal-card__card-img-icon {
      display: none;
    }

    img.modal-card__card-img-icon-mobile {
      display: block;
      top: -5px;
      left: -5px;
      width: 36px;
      height: 36px;
    }
  }

  .modal-card__feedback {
    a {
      white-space: nowrap;
    }
  }

  .modal__form-simple {
    margin-bottom: 24px;

    form {
      flex-direction: column;
    }

    .modal__form-input-block {
      margin-bottom: 16px;
    }

    .modal__form-input {
      margin-bottom: 0;

      label {
        margin-bottom: 0;
      }
    }

    .modal__form-bottom {
      margin-left: 0;
    }
  }

  .modal__form-bottom-close {
    margin-bottom: 24px;
  }

  .modal__form-policy {
    font-size: 13px;
  }
}
